.toggle {
    position: relative;
    display: block;
    width: 46px;
    height: 20px;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transform: translate3d(0, 0, 0)
}

.toggle::before {
    content: "";
    position: relative;
    top: 0;
    right: 0;
    width: 46px;
    height: 27px;
    display: block;
    background: #9a9999;
    border-radius: 15px;
    transition: background .2s ease
}

.toggle span {
    position: absolute;
    top: 2px;
    left: 0;
    width: 23px;
    height: 23px;
    display: block;
    background: #fff;
    border-radius: 100%;
    box-shadow: 0 3px 8px rgba(154, 153, 153, .5);
    transition: all .2s ease
}

#cbx:checked+.toggle:before {
    background: #feb7e1
}

.cbx1:checked+.toggle:before {
    background: #feb7e1
}

input:checked+.toggle span {
    background: #ec008c;
    transform: translateX(20px);
    transition: all .2s cubic-bezier(.8, .4, .3, 1.25), background .15s ease;
    box-shadow: 0 3px 8px rgba(79, 46, 220, .2)
}

.cbx1:checked+.toggle span {
    background: #ec008c;
    transform: translateX(20px);
    transition: all .2s cubic-bezier(.8, .4, .3, 1.25), background .15s ease;
    box-shadow: 0 3px 8px rgba(79, 46, 220, .2)
}

input:checked+.toggle span:before {
    transform: scale(1);
    opacity: 0;
    transition: all .4s ease
}

.cbx1:checked+.toggle span:before {
    transform: scale(1);
    opacity: 0;
    transition: all .4s ease
}

